import Header from "../../../components/Header.vue"
import interfaces from "../../../unils/interfaces.js"
export default {
	data() {
		return {
			goodsList: [],
			showGoodListIndex: 0
		}
	},
	components: {
		Header
	},
	onLoad() {
		this.initData()
	},
	methods: {
		initData() {
			this.request({
				url: interfaces.getGoodsList,
				success: ((res) => {
					// console.log("goodList", res)
					this.goodsList = res.data
					console.log("this.goodlist", this.goodsList)
				})
			})
		},
		handleCategory(index) {
			console.log(index)
			this.showGoodListIndex = index;
		},
		handleCategoryList(item) {
			// console.log(item.name)
			uni.navigateTo({
				url: `../../goods/goods-list?name=${item.name}`
			})
		},
	}
}
